.navbar {
  height: 100vh;
  width: 100vw !important
}

.text-in-header-for-homepage {
  font-family: 'Times New Roman', Times, serif;
}

.div-tag-for-navbar {
  width: calc(100% - 80%);
  background-color: aliceblue;
  height: 100%;
  text-align: center;
  overflow-y: scroll;
  position: fixed;
}

.image-in-sidebar {
  border-radius: 50%;
  width: 100px;
  height: 130px;
  margin-top: 20px;
}

.sub-tag-in-navbar-text {
  margin-top: -17px;
  font-size: small;
  font-family: cursive;
}

.unorder-list-tags {
  list-style-type: none;
  margin-right: 40px;
}

.unorder-list-tags li {
  padding-bottom: 10px;
}

.main-div-in-rec {
  display: flex;
}

.This-is-secnd-container {
  margin-left: 20%;
  overflow: hidden;
}

.this-is-for-another-div-for-overall-in-edge {
  display: flex;
}

.anotehr-image-in-next-tab-vgiew {
  width: 79vw;
}

.active-tab {
  color: #2c98f0 !important;
  text-decoration: underline;
}

.unorder-list-tags li:not(.active) {
  position: relative;
}

.unorder-list-tags li:not(.active):hover {
  text-decoration: underline;
  text-decoration-color: #2c98f0;
}

.small {
  font-size: 85%;
  color: #d1d1d1;
}

.unorder-list-tags li {
  margin: 0 0 10px 0;
  padding: 0;
  list-style: none;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.unorder-list-tags li {
  font-family: "Quicksand", Arial, sans-serif;
  font-weight: 400;
  line-height: 1.8;
  color: rgba(0, 0, 0, 0.7);
}

.hamburger-menu {
  display: none;
  cursor: pointer;
  padding: 10px;
  position: fixed;
  top: 20px;
  left: 5px;
  z-index: 1000;
}

.bar {
  width: 25px;
  height: 3px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}

@media screen and (max-width: 767px) {
  .hamburger-menu {
    display: block;
  }

  .main-div-in-rec.mobile-menu-open .bar:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .main-div-in-rec.mobile-menu-open .bar:nth-child(2) {
    opacity: 0;
  }

  .main-div-in-rec.mobile-menu-open .bar:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
  }

  .div-tag-for-navbar {
    z-index: 1;
    width: 100%;
    position: fixed;
    top: 0;
    left: -100%;
    height: 100%;
    text-align: center;
    overflow-y: scroll;
    transition: left 0.3s ease;
  }

  .div-tag-for-navbar.active {
    left: 0;
  }

  .image-in-sidebar {
    margin-top: 10px;
  }

  .unorder-list-tags {
    margin-right: 40px;
  }

  .unorder-list-tags li {
    padding: 15px;
    color: #000000;
  }

  .small {
    position: fixed;
    bottom: 0;
    width: 100%;
    text-align: center;
  }

  .This-is-secnd-container {
    overflow: visible;
    margin-left: -20%;
  }
}